import { ADD_REC_URL, DEL_REC_URL, UPDATE_REC_URL, GET_REC_URL } from "./URL";
import axios from "axios";

export const GetRecords = async (data) => {
  return axios.get(GET_REC_URL);
};

export const AddRecord = (data) => {
  return axios.post(ADD_REC_URL, data, {
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json"
    }
  });
};

export const DeleteRecord = (data) => {
  return axios.post(DEL_REC_URL, data, {
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json"
    }
  });
};

export const UpdateRecord = (data) => {
  return axios.post(UPDATE_REC_URL, data, {
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json"
    }
  });
};
