import "semantic-ui-css/semantic.min.css";
import React, { Component } from "react";
import { Button, Form, Container, Header } from "semantic-ui-react";
import { BasicSearch } from "./Table";
import "../styles.css";
import { ImagesComponent } from "./ImagesComponent";
import { AddRecord, UpdateRecord } from "../util/api";
import { SimpleModal } from "./Modal";
export default class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      karyakarini: "",
      taluka: "",
      mobile: "",
      district: "",
      address: "",
      designation: "",
      showRegistrationForm: false,
      showTable: false,
      isAlert: false,
      isUpdating: false
    };
  }

  changeHandler = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  submitHandler = (e) => {
    e.preventDefault();
    console.log(this.state);

    let fun;
    if (this.state.isUpdating) {
      fun = UpdateRecord;
    } else {
      fun = AddRecord;
    }
    if (
      this.state.name === "" ||
      this.state.designation === "" ||
      this.state.address === ""
    ) {
      alert("Please enter name, address, and designation at least");
    } else {
      this.setState({
        isAlert: true
      });
      fun(this.state).then((response) => {
        this.resetData();
        setTimeout(() => {
          this.setState({
            isAlert: false
          });
        }, 1000);
        console.log(response);
      });
    }
  };

  setUpdateRecord = (rec) => {
    const {
      name,
      uid,
      taluka,
      karyakarini,
      address,
      mobile,
      district,
      designation
    } = rec;
    this.setState({
      uid,
      taluka,
      name,
      karyakarini,
      address,
      mobile,
      district,
      designation,
      showRegistrationForm: true,
      showTable: false,
      isUpdating: true
    });
  };

  resetData() {
    this.setState({
      name: "",
      taluka: "",
      mobile: "",
      karyakarini: "",
      district: "",
      address: "",
      designation: "",
      isUpdating: false
    });
  }

  displayHomePage() {
    return (
      <div className="manPage">
        <marquee
          width="80%"
          style={{ margin: "20px", color: "blue", fontSize: "20px" }}
        >
          {
            "Site is under construction let us know if any updation (Suggession) please contact to Sangharsh Sovitkar Mo.No 9922363899 "
          }
        </marquee>
        <ImagesComponent />
        <div
          style={{
            border: "1px solid black",
            background: "lightgrey",
            color: "red",
            fontWeight: "bold",
            width: "300px"
          }}
        >
          {
            "ग्रुप चा उद्देश एकच आहे की म.रा.जैन अल्पसंख्यांक कार्यकारिणी अंतर्गत आपल्या राज्यातील जिल्हा व तालुका कार्यकारिणी पदाधिकारी सदस्य यांचा एकमेकांशी संपर्क रहावा व विचारांची देवाणघेवाण व्हावी."
          }
        </div>

        <div>
          <Button
            color="blue"
            onClick={() => {
              this.setState({
                showTable: true
              });
            }}
            type="submit"
          >
            Contact List
          </Button>
        </div>
        <div>
          <Button
            onClick={() => {
              alert(
                "The admin can only add the entry, please contact to Vinod Sovitkar 9823405966"
              );
            }}
            color="blue"
            type="submit"
          >
            Member Registration
          </Button>
        </div>
        <div className="footer">Copyright 2021-2022</div>
      </div>
    );
  }

  render() {
    const {
      showRegistrationForm,
      name,
      karyakarini,
      taluka,
      address,
      mobile,
      district,
      designation,
      isAlert,
      showTable
    } = this.state;
    if (!showRegistrationForm && !showTable) {
      return <div className="mainPageContainer">{this.displayHomePage()}</div>;
    }
    if (showTable) {
      return (
        <>
          <button
            style={{ display: "flex", background: "lightblue" }}
            onClick={() => {
              this.setState({
                showTable: false
              });
            }}
          >
            {"< = back"}
          </button>
          <BasicSearch setUpdateRecord={this.setUpdateRecord} />
        </>
      );
    }

    return (
      <>
        <button
          style={{ display: "flex", background: "lightblue" }}
          onClick={() => {
            this.setState({
              showRegistrationForm: false
            });
          }}
        >
          {"<= back"}
        </button>
        <Container fluid className="container">
          <Header as="h2">Jain Minority Member Registration Form</Header>
          <Header as="h4">
            (Please fill the info in English so it will easy while searching)
          </Header>
          {isAlert && (
            <div className="alert">
              {
                "Record has been saved successfully  you can check on Records..."
              }
            </div>
          )}
          <div className="formWrapper">
            <Form className="form" onSubmit={this.submitHandler}>
              <Form.Field>
                <label>Name</label>
                <input
                  placeholder="Enter your name"
                  type="text"
                  name="name"
                  value={name}
                  onChange={this.changeHandler}
                />
              </Form.Field>
              <Form.Field>
                <label>Designation</label>
                <input
                  placeholder="Enter your designation"
                  type="text"
                  name="designation"
                  value={designation}
                  onChange={this.changeHandler}
                />
              </Form.Field>
              <Form.Field>
                <label>Level of Designation </label>
                <input
                  placeholder="e.g. Taluka level, district level...."
                  type="text"
                  name="karyakarini"
                  value={karyakarini}
                  onChange={this.changeHandler}
                />
              </Form.Field>
              <Form.Field>
                <label>Address</label>
                <input
                  placeholder="Enter your address"
                  type="text"
                  name="address"
                  value={address}
                  onChange={this.changeHandler}
                />
              </Form.Field>
              <Form.Field>
                <label>Taluka</label>
                <input
                  placeholder="Enter your taluka"
                  name="taluka"
                  value={taluka}
                  onChange={this.changeHandler}
                />
              </Form.Field>
              <Form.Field>
                <label>Mobile</label>
                <input
                  placeholder="Enter your mobile"
                  name="mobile"
                  value={mobile}
                  onChange={this.changeHandler}
                />
              </Form.Field>
              <Form.Field>
                <label>District</label>
                <input
                  placeholder="Enter your district"
                  type="text"
                  name="district"
                  value={district}
                  onChange={this.changeHandler}
                />
              </Form.Field>
              {
                //   <Form.Field>
                //   <label>Image</label>
                //   <FileInputComponent
                //     labelText="Select Photo file"
                //     labelStyle={{ fontSize: 14 }}
                //     callbackFunction={(file_arr) => {
                //       console.log(file_arr[0].base64);
                //       // base64ToBufferAsync(file_arr[0].base64, (arr) => {
                //       //   // this.setState({
                //       //   //   imageUrl: arr
                //       //   // });
                //       // });
                //     }}
                //     accept="image/*"
                //   />
                // </Form.Field>
              }

              <Button color="blue" type="submit">
                Submit
              </Button>
            </Form>
          </div>
        </Container>
      </>
    );
  }
}
