import React from 'react';
import { useState, useEffect } from "react";
import Carousel from "react-img-carousel";
import { GET_IMAGE_URl } from "../util/URL";
require("react-img-carousel/lib/carousel.css");

const ImageArr = [1, 2, 3, 4, 5, 6, 7, 8];

const LoadingComponent = () => {
  return (
    <svg
      version="1.1"
      id="Layer_1"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      x="0px"
      y="0px"
      width="54px"
      height="150px"
      viewBox="0 0 24 30"
      style={{ enableBackground: "new 0 0 50 50" }}
      xmlSpace="preserve"
    >
      <rect x="0" y="10" width="4" height="10" fill="#333" opacity="0.2">
        <animate
          attributeName="opacity"
          attributeType="XML"
          values="0.2; 1; .2"
          begin="0s"
          dur="0.6s"
          repeatCount="indefinite"
        />
        <animate
          attributeName="height"
          attributeType="XML"
          values="10; 20; 10"
          begin="0s"
          dur="0.6s"
          repeatCount="indefinite"
        />
        <animate
          attributeName="y"
          attributeType="XML"
          values="10; 5; 10"
          begin="0s"
          dur="0.6s"
          repeatCount="indefinite"
        />
      </rect>
      <rect x="8" y="10" width="4" height="10" fill="#333" opacity="0.2">
        <animate
          attributeName="opacity"
          attributeType="XML"
          values="0.2; 1; .2"
          begin="0.15s"
          dur="0.6s"
          repeatCount="indefinite"
        />
        <animate
          attributeName="height"
          attributeType="XML"
          values="10; 20; 10"
          begin="0.15s"
          dur="0.6s"
          repeatCount="indefinite"
        />
        <animate
          attributeName="y"
          attributeType="XML"
          values="10; 5; 10"
          begin="0.15s"
          dur="0.6s"
          repeatCount="indefinite"
        />
      </rect>
      <rect x="16" y="10" width="4" height="10" fill="#333" opacity="0.2">
        <animate
          attributeName="opacity"
          attributeType="XML"
          values="0.2; 1; .2"
          begin="0.3s"
          dur="0.6s"
          repeatCount="indefinite"
        />
        <animate
          attributeName="height"
          attributeType="XML"
          values="10; 20; 10"
          begin="0.3s"
          dur="0.6s"
          repeatCount="indefinite"
        />
        <animate
          attributeName="y"
          attributeType="XML"
          values="10; 5; 10"
          begin="0.3s"
          dur="0.6s"
          repeatCount="indefinite"
        />
      </rect>
    </svg>
  );
};

export const ImagesComponent = () => {
  const [startWith, changeStart] = useState(0);
  const imgArr = ImageArr.slice(startWith, 4);
  const [imageArray, updateImageArray] = useState([]);
  useEffect(() => {
    fetch(GET_IMAGE_URl)
      .then((res) => res.json())
      .then((res) => updateImageArray(res));
  }, []);
  console.log(imageArray);
  if (!imageArray.length) {
    return <LoadingComponent />;
  }
  return (
    imageArray.length && (
      <Carousel cellPadding={15}>
        {imageArray.map((data) => (
          <div
            style={{
              display: "flex",
              textAlign: "center",
              color: "blue",
              fontWeight: "bold",
              flexDirection: "column",
              border: "2px solid grey",
              alignItems: "center",
              borderRadius: "12px ",
            }}
          >
            <img width="180px" height="190px" src={data.url} alt="image" />
            <span>{data.name}</span>
            <span style={{ color: "red" }}>{data.des}</span>
          </div>
        ))}
      </Carousel>
    )
  );
};
